import React from 'react';

import styles from './benefits.module.scss';
import bgImg from '../../assets/img/benefits-bg.png';

const Benefits = () => {
    return (
        <section className={styles['section']}>
            <div className="container">
                <h2 className={styles['section__title']}>  </h2>
                <span className={styles['section__subtitle']}>        ,      :</span>
                <ul className={styles['section__list']}>
                    <li className="section__item">    ,  ,   .</li>
                    <li className="section__item">    ( + )   2 .</li>
                    <li className="section__item"> ,        .</li>
                    <li className="section__item">    .</li>
                    <li className="section__item"> -   ( ).</li>
                </ul>
                <img src={bgImg} alt="background" className={styles['section__bg']} />
            </div>
        </section>
    );
}

export default Benefits;