import React from 'react';
import { Link } from 'react-router-dom';

import styles from './nav.module.scss';

const Nav = () => {
  return (
    <nav className={styles.top}>
        <div className="container">
            <div className={styles['top-wrap']}>
                <span className={styles['work-time']}>-: 8:0020:00 : 9:0020:00</span>
                <ul className={styles['top__list']}>
                    <li className={styles['top__list-item']}></li>
                    <li className={styles['top__list-item']}></li>
                    <Link to="comments" className={styles['top__list-item']}></Link>
                    <Link to="blog" className={styles['top__list-item']}></Link>
                    <li className={styles['top__list-item']}>
                        
                        <div className={styles['top__dropdown']}>
                            <ul className={styles['dropdown__list']}>
                                <li className={styles['dropdown__item']}></li>
                                <li className={styles['dropdown__item']}>  </li>
                                <li className={styles['dropdown__item']}> </li>
                                <Link to='info' className={styles['dropdown__item']}> </Link>
                            </ul>
                        </div>
                    </li>
                    <li className={styles['top__list-item']}></li>
                </ul>
                <div className={styles.socials}>
                    <a href='#' className={`icon-vk-logo ${styles.vk}`}></a>
                    <a href='#' className={`icon-insta-logo ${styles.insta}`}></a>
                    <Link to='account' className={`icon-user ${styles.user}`}> </Link>
                </div>
            </div>
        </div>
    </nav>
  )
}


export default Nav;